.page-header {
  width: 288px;
  background-color: #222124;

  &__box {}

  &__logo {
    display: block;
    height: 80px;
    display: flex;
    align-items: center;
    padding-left: 28px;
    border-bottom: 1px solid #3F4253; }

  &__logo-img {}

  &__nav {} }

.header-nav {

  &__list {}

  &__item {}

  &__link {
    color: #fff;
    display: block;
    border-bottom: 1px solid #3F4253;
    padding-left: 28px;
    padding-top: 13px;
    padding-bottom: 13px; }

  &__link--active {
    position: relative;
    background-color: #19181B;

    &:hover {
      opacity: 1; }

    &::before {
      content: "";
      position: absolute;
      width: 5px;
      height: 100%;
      left: 0;
      top: 0;
      background-color: #C74B4E; } } }
